body { background: #898989; line-height: 1.4; color: #000; font-size: 16px; font-family: "Circe", Helvetica, sans-serif; }

.page { display: flex; flex-direction: column; height: 100vh; }

.inner { max-width: 100%; padding: 0 100px; }

@media only screen and (max-width: 767px) { .inner { padding: 0 87px; } }

.block { display: flex; }

.main { padding-top: 50px; flex: 1 0 auto; background-repeat: no-repeat; background-position: center; background-size: cover; overflow: hidden; }

@media only screen and (max-width: 1600px) { .main { padding-top: 25px; } }

@media only screen and (max-width: 767px) { .main { padding-top: 190px; } }

@media only screen and (max-width: 599px) { .main { padding-top: 110px; } }

.main__inner { height: 100%; display: flex; justify-content: center; }

.main__content { position: relative; max-width: 665px; margin: 0 auto; padding: 30px 28px 60px; display: flex; flex-direction: column; z-index: 10; }

@media only screen and (max-width: 1600px) { .main__content { padding: 20px 28px 40px; } }

.main__title { flex: 0 0 auto; margin-bottom: 28px; font-size: 53px; font-weight: 700; line-height: 1; text-align: center; color: #fff; text-shadow: 0 3px 11px #000; }

@media only screen and (max-width: 1600px) { .main__title { font-size: 45px; margin-bottom: 20px; } }

@media only screen and (max-width: 599px) { .main__title { font-size: 25px; } }

.main__title span { display: inline-block; background: -webkit-linear-gradient(#DA7D0F, #C05302); -webkit-background-clip: text; -webkit-text-fill-color: transparent; text-shadow: none; position: relative; }

.main__title span:after { position: absolute; content: attr(data-text); left: 0; top: 0; text-shadow: 0 3px 11px #000; z-index: -1; }

.main__form { flex: 1 0 auto; }

.main__bg { position: absolute; left: 0; top: 0; right: 0; bottom: 110px; background-size: cover; background-position: center; background-repeat: no-repeat; opacity: 0.84; z-index: -5; border-radius: 5px; border: 3px solid #bca133; }

@media only screen and (max-width: 1600px) { .main__bg { bottom: 70px; } }

.main__bg:before { content: ''; position: absolute; left: 0; top: 0; bottom: 0; right: 0; background-color: rgba(0, 0, 0, 0.5); z-index: 1; }

.main__coins { position: absolute; z-index: -7; top: -50px; left: -260px; right: -225px; }

@media only screen and (max-width: 767px) { .main__coins { top: -140px; left: -200px; right: -178px; } }

.main__coins img { display: block; max-width: 100%; }

.main__statue { position: absolute; top: 60px; width: 295px; z-index: -10; }

@media only screen and (max-width: 767px) { .main__statue { top: -180px; } }

@media only screen and (max-width: 599px) { .main__statue { width: 200px; top: -110px; } }

.main__statue img { display: block; max-width: 100%; }

.main__statue_gor { left: -185px; }

@media only screen and (max-width: 767px) { .main__statue_gor { left: -65px; } }

@media only screen and (max-width: 599px) { .main__statue_gor { left: -40px; } }

.main__statue_anubis { right: -190px; }

@media only screen and (max-width: 767px) { .main__statue_anubis { right: -85px; } }

@media only screen and (max-width: 599px) { .main__statue_anubis { right: -40px; } }

.form { height: 100%; display: flex; flex-direction: column; }

.form__top { flex: 1 0 auto; }

.form__bottom { flex: 0 0 auto; padding: 20px 40px 0; }

@media only screen and (max-width: 599px) { .form__bottom { padding: 35px 0 0; } }

.form-control { position: relative; }

.form-control:not(:first-child) { margin-top: 20px; }

.form-input { width: 100%; height: 70px; padding: 23px 14px; outline: none; font-family: "Circe", Helvetica, sans-serif; font-size: 25px; font-weight: 300; color: #000; border: 3px solid transparent; border-radius: 3px; background-color: #fff; transition: border-color .3s; }

.form-input::placeholder { font-family: "Circe", Helvetica, sans-serif; font-size: 25px; font-weight: 300; color: #000; }

@media only screen and (max-width: 1600px) { .form-input::placeholder { font-size: 20px; } }

@media only screen and (max-width: 599px) { .form-input::placeholder { font-size: 18px; } }

@media only screen and (max-width: 1600px) { .form-input { height: 50px; font-size: 20px; } }

@media only screen and (max-width: 599px) { .form-input { height: 40px; font-size: 18px; padding: 0 7px; } }

.not-valid .form-input { border-color: #db0100; }

.valid .form-input { border-color: #68b645; }

.form__submit { margin-top: 23px; display: flex; justify-content: center; }

.form__btn { width: 335px; height: 100px; font-family: "Acrom", Helvetica, sans-serif; font-weight: 800; background-image: url("../img/btn-bg.png"); background-color: transparent; background-size: cover; background-position: center; background-repeat: no-repeat; border-radius: 50px; color: #fff; font-size: 32px; box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.7); }

@media only screen and (max-width: 1600px) { .form__btn { width: 250px; height: 60px; font-size: 28px; } }

.form__btn span { position: relative; z-index: 10; text-transform: uppercase; text-shadow: 0 0 6px rgba(0, 0, 0, 0.3); }

.checkbox { cursor: pointer; display: block; }

.checkbox input { display: none; }

.checkbox input:checked ~ .checkbox__text:before { box-shadow: inset 0 0 0 3px #68b645; }

.checkbox input:checked ~ .checkbox__text:after { opacity: 1; }

.checkbox__text { position: relative; display: inline-block; padding-left: 80px; font-size: 24px; color: #fff; text-align: center; line-height: 1; }

@media only screen and (max-width: 1023px) { .checkbox__text { font-size: 18px; padding-left: 50px; } }

@media only screen and (max-width: 599px) { .checkbox__text { font-size: 14px; padding-left: 40px; } }

.checkbox__text:before { content: ''; position: absolute; left: 0; top: 50%; width: 44px; height: 44px; transform: translateY(-50%); border-radius: 3px; background-color: #CFCFCF; transition: box-shadow .3s; }

@media only screen and (max-width: 1023px) { .checkbox__text:before { width: 38px; height: 38px; } }

@media only screen and (max-width: 599px) { .checkbox__text:before { width: 30px; height: 30px; } }

.checkbox.not-valid .checkbox__text:before { box-shadow: inset 0 0 0 3px #db0100; }

.checkbox__text:after { content: ''; position: absolute; display: inline-block; left: 15px; top: 50%; width: 16px; height: 32px; border: solid #000; border-width: 0px 3px 3px 0; transform: translateY(-65%) rotate(45deg); opacity: 0; transition: opacity .3s; }

@media only screen and (max-width: 1023px) { .checkbox__text:after { width: 12px; height: 24px; left: 14px; } }

@media only screen and (max-width: 599px) { .checkbox__text:after { left: 10px; width: 10px; height: 20px; } }

.checkbox__link { display: block; color: #fff; text-decoration: none; }

.no-touch .checkbox__link:hover { text-decoration: underline; }

.error { position: absolute; left: 0; top: 100%; color: #fff; font-size: 18px; line-height: 1; text-shadow: 0px 3px 11px #000; }

@media only screen and (max-width: 1023px) { .error { font-size: 14px; font-weight: 300; } }

.footer { padding: 40px 0 25px; flex: 0 0 auto; background-color: #000; color: #fff; }

@media only screen and (max-width: 1600px) { .footer { padding: 20px 0; } }

@media only screen and (max-width: 767px) { .footer { padding: 50px 0; } }

@media only screen and (max-width: 599px) { .footer { padding: 25px 0; } }

.footer__inner { height: 100%; }

@media only screen and (max-width: 1600px) { .footer__inner { padding: 0 40px; } }

@media only screen and (max-width: 1259px) { .footer__inner { padding: 0 25px; } }

.footer__content { display: flex; align-items: center; justify-content: space-between; height: 100%; }

@media only screen and (max-width: 767px) { .footer__content { justify-content: center; } }

.footer__logo { flex: 0 0 auto; font-size: 86px; line-height: 1; }

@media only screen and (max-width: 1600px) { .footer__logo { font-size: 60px; } }

@media only screen and (max-width: 1259px) { .footer__logo { font-size: 50px; } }

@media only screen and (max-width: 767px) { .footer__logo { font-size: 86px; } }

@media only screen and (max-width: 599px) { .footer__logo { font-size: 50px; } }

.footer__partners { flex: 1 1 auto; padding: 0 90px; height: 100%; display: flex; flex-wrap: wrap; align-items: center; justify-content: center; }

@media only screen and (max-width: 1259px) { .footer__partners { padding-right: 0; padding-left: 30px; } }

@media only screen and (max-width: 767px) { .footer__partners { display: none; } }

.partners__item { margin-right: 18px; }

.partners__item img { display: block; max-width: 100%; }

.partners__age-limit { width: 100%; display: flex; align-items: center; justify-content: center; }

.partners__age-limit img { display: block; max-width: 100%; }

@media only screen and (max-width: 1259px) { .lg-hide { display: none; } }

[data-modal] { position: fixed; z-index: -1; top: 0; left: 0; right: 0; bottom: 0; opacity: 0; visibility: hidden; min-width: 320px; text-align: center; font-size: 0; transform: translate3d(0, 0, 0); overflow: auto; transition: opacity 0.3s, visibility 0.3s, z-index 0.3s; background-color: rgba(0, 0, 0, 0.5); }

[data-modal]:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

[data-modal].is-open { opacity: 1; visibility: visible; z-index: 100; }

[data-modal-container] { position: relative; z-index: 1; vertical-align: middle; display: inline-block; max-width: 1000px; margin: 0 auto; }

.modal-error { padding: 79px 10px 87px; width: 100%; max-width: 663px; background-color: #71225d; color: #fff; }

@media only screen and (max-width: 767px) { .modal-error { padding: 40px 15px; } }

.modal-error__text { font-size: 24px; line-height: 3; }

.modal-success { padding: 50px 0 25px; width: 100%; max-width: 791px; background-color: #71225d; color: #fff; }

@media only screen and (max-width: 767px) { .modal-success { padding: 40px 15px; } }

.modal-success__title { padding-bottom: 40px; font-size: 43px; line-height: 1.2; text-transform: uppercase; }

@media only screen and (max-width: 767px) { .modal-success__title { font-size: 28px; padding-bottom: 20px; } }

.modal-success__logo { height: 160px; margin-bottom: 24px; display: flex; align-items: center; justify-content: center; font-size: 86px; background-color: #1b1b1b; }

@media only screen and (max-width: 767px) { .modal-success__logo { font-size: 50px; } }

.modal-success__action { margin-bottom: 36px; font-size: 24px; line-height: 1.2; font-weight: 700; }

@media only screen and (max-width: 767px) { .modal-success__action { margin-bottom: 20px; font-size: 18px; } }

.modal-success__hint { padding: 0 104px 27px; font-size: 18px; line-height: 1.2; color: #ffffff; }

@media only screen and (max-width: 767px) { .modal-success__hint { padding: 0 0 20px; font-size: 16px; } }

.modal-success__wish { margin-bottom: 34px; font-size: 24px; font-weight: 700; line-height: 1.2; text-transform: uppercase; }

@media only screen and (max-width: 767px) { .modal-success__wish { margin-bottom: 20px; font-size: 20px; } }

.modal-success__btn { display: inline-block; padding: 32px 75px 30px; border: 2px solid #924e9d; background-image: linear-gradient(45deg, #4f2169 0%, #c31e41 100%); text-decoration: none; }

@media only screen and (max-width: 767px) { .modal-success__btn { padding: 32px 0 30px; width: 100%; } }

.modal-success__btn-text { color: #fff; font-family: "Circe", Helvetica, sans-serif; font-size: 24px; font-weight: 700; line-height: 1; text-transform: uppercase; }

@media only screen and (max-width: 767px) { .modal-success__btn-text { font-size: 20px; } }
