//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Circe
@font-face {
	@include fontdefault(normal, 200, 'Circe');
	@include fontpath('Circe Extra-Light', 'Circe-ExtraLight', Circe-ExtraLight); }
@font-face {
	@include fontdefault(normal, 300, 'Circe');
	@include fontpath('Circe Light', 'Circe-Light', Circe-Light); }
@font-face {
	@include fontdefault(normal, 400, 'Circe');
	@include fontpath('Circe', 'Circe-Regular', Circe-Regular); }
@font-face {
	@include fontdefault(normal, 700, 'Circe');
	@include fontpath('Circe Bold', 'Circe-Bold', Circe-Bold); }
@font-face {
	@include fontdefault(normal, 800, 'Circe');
	@include fontpath('Circe Extra-Bold', 'Circe-Extrabold', Circe-Extrabold); }

// Acrom
@font-face {
	@include fontdefault(normal, 800, 'Acrom');
	@include fontpath('Acrom Extra-Bold', 'Acrom-Extrabold', Acrom-ExtraBold); }
